.main>.warapper{
  
}
.column_1{
  grid-column-start: 1;
  grid-column-end: 4;
}
.column_2{
  grid-column-start: 4;
  grid-column-end: 13;
  
}
.nav__catalog{
  background: #BED1E1;
  border-radius: 3px;
  padding: 28px 0px 28px 28px;
  margin-bottom: 30px;
}
.catalog__title{
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 16px;
}
.catalog__item{
  position: relative;
  padding: 8px 0;
  padding-right: 32px;
  margin-bottom: 7px;
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  transition: all .3s;
  cursor: pointer;
  color: #404040;
  z-index: 50;

  &:hover{
    color: #FFFFFF;

    &>.catalog_item__list{
      transform: scaleX(1);
      opacity: 1;
    }
  }
}
.catalog_item__list{
  position: absolute;
  transition: all .3s;
  transform: scaleX(0);
  transform-origin: 0 0;
  opacity: 0;
  left: 100%;
  top: 0;
  padding: 19px;
  background: #FFFFFF;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.25);
  border-radius: 3px;
  cursor: default;
}

.catalog_item__item{
  white-space: nowrap;
  color: #404040;
  font-family: 'Fira Sans';
  padding: 5px;
  margin-bottom: 5px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  cursor: pointer;
  transition: all .3s;
  &:hover{
    color:#566E98;
  }
  &:last-child{
    margin-bottom: 0;
  }
}
