.section__promitions{
  margin-bottom: 50px;
}
.promitions__head{
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  align-items: flex-end;
}
.promitions__title{
  width: min-content;
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 24px;
  line-height: 24px;
  color: #404040;
}

.linck__all_promititons{
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  color: #566E98;
}
.promitions__list{
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  justify-content: space-around;
}
.promitions__item{
  width: 260px;
}
.promitions__img{
  width: 260px;
  height: 200px;
}
.promition__wrap_info{
  position: relative;
  height: 64px;
  padding-top: 23px;
  background-color: rgba(255, 255, 255, 0);
}
.promition__date{
  position: absolute;
  top: -13px;
  left: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 27px;
  background: #BED1E1;
  border-radius: 3px;

  font-family: 'Fira Sans';
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;

  /* Текст */
  color: #404040;
  }
.promition__title{
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #566E98;
  cursor: pointer;
  transition: all .3s;
  &:hover{
    color: #E1CEBE;
  }
}