*{
	box-sizing: border-box;
	text-decoration: none;
	padding: 0;
	margin: 0;
	outline: none;
}
a:hover{
	text-decoration: none;
}

body{
	width: 100%;
	height: 100%;
	font-size: 16px;
	//background-color: #fff;
	
}
.body{
	min-width: 780px;
}
html{
	width: 100%;
	min-height: 100%;
	height: 1px;

	@include phones{
		font-size: 14px;
	}
}
body {
	font-size: 16px;
	line-height: 1.42;
	-webkit-font-smoothing: antialiased;
	font-family: Arial;
	min-width: 360px;
}


h1, h2, h3, h4, h4, h5{
	margin: 0;
	font-weight: normal;
}

li{
	text-decoration: none;
}

.wrapper{
	max-width: 1155px;
	
	padding: 0 20px;
	margin: 0 auto;
  display: grid;
	grid-template-columns: repeat(12, minmax(40px, 1fr));
	column-gap: 25px;
}

