.maincontent {
	@extend %clearfix;
}

.container {
	position: relative;
	display: flex;
	flex-direction: column;
	min-width: 360px;

	@include tablets{
		min-height: 500px;
	}
}

li {
	list-style: none;
}

ul {
	padding: 0;
	margin: 0;
}

p {
	margin: 0;
	-webkit-hyphens: auto;
	-moz-hyphens: auto;
	-ms-hyphens: auto;
	hyphens: auto;
}

* {
	box-sizing: border-box;
}

.section{
	min-width: 360px;
}
