.footer{
  width: 100%;
  padding: 17px 0px;
  background: #BED1E1;
  &>.wrapper{
    display: flex;
    flex-direction: column;
  }
}
.footer__top{
  display: grid;
  grid-template-columns: repeat(12, minmax(40px, 1fr));
  column-gap: 25px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 11px;
}
.footer__logo_block{
  grid-column-start: span 3;
}
.footer__logo_img{
  display: block;
  height: 101px;
  width: 127px;
  mask-image: url('/src/assets/img/logo.svg');
  background-color: #fff;
}
.footer__address{
  grid-column-start: span 9;
  display: flex;
  align-items: center;
  margin-bottom: 0;
}
.footer_address__wrap{
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  height: min-content;
}
.footer__address__city{
  background-image: url('/src/assets/img/location-marker.svg');
  background-repeat: no-repeat;
  padding-left: 36px;

  font-family: 'Fira Sans';
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0.02em;
  color: #404040;

}
.footer__address__email{
  background-image: url('/src/assets/img/mail.svg');
  background-repeat: no-repeat;
  padding-left: 36px;

  font-family: 'Fira Sans';
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0.02em;
  color: #404040;
}

.footer__address__phone{
  background-image: url('/src/assets/img/phone.svg');
  background-repeat: no-repeat;
  padding-left: 30px;
  
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: 0.02em;
  color: #404040;
}
.footer__bottom{
  padding-top: 7px;
  display: flex;
  justify-content: space-between;
}
.footer__text{
  width: 50%;
  height: 100%;
  &>p{
    font-family: 'Fira Sans';
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    color: #404040;
    &>span{
      margin-left: 15px;
    }
  }
}

.footer__make_site{
  height: 100%;
  display: flex;
  align-items: center;
}
.make_site__text{
  font-family: 'Mplus 1p';
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  margin-right: 17px;
}
.logo__img__kontur{
  display: block;
  background-image: url('/src/assets/img/Kontur.svg');
  height: 39px;
  width: 58px;
}