.section__popular_products{
  display: flex;
  flex-direction: column;
  margin-bottom: 28px;
}
.popular_products__title{
  font-family: 'Fira Sans';
  font-weight: 500;
  margin-bottom: 21px;
  font-size: 24px;
  line-height: 29px;
  display: flex;
  align-items: center;
  color: #404040;
}
.products__list{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  gap: 25px;
 

}
.products__item{
  width: 100%;
  width: 260px;
  background: #EBEDEE;
  border-radius: 3px;
  overflow: hidden;

}
.product__img{
  display: block;
  width: 260px;
  height: 260px;
  background-image: url('/src/assets/img/emptyPP.svg');
}
.product__info{
  padding: 20px 11px 29px 11px;
}
.product__text{
  height: 65px;
  font-family: 'Fira Sans';
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  padding: 0 10px;
  border-bottom: 1px solid #FFFFFF;
}
.product__price{
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Fira Sans';
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  border-bottom: 1px solid #FFFFFF;

}
.car__block_input{
  height: 58px;
  margin-top: 17px;
  display: flex;
  justify-content: space-between;
}
.input__wrap{
  position: relative;
  width: 95px;
  height: 40px;
  border-radius: 3px;
  overflow: hidden;
}
.input__plus, .input__minus{
  position: absolute;
  top: 13px;
  height: 15px;
  width: 15px;
  mask-size: 15px 15px;
  background-color: #566E98;
  cursor: pointer;
}
.input__plus{
  right: 11px;
  mask-image: url('/src/assets/img/plus.svg');
}

.input__minus{
  left: 11px;
  mask-image: url('/src/assets/img/minus.svg');
}
.input_car__count{  
  appearance: none;
  margin: 0;
  padding: 0 35px;
  border: none;
  height: 100%;
  width: 100%;
  text-align: center;

  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  color: #404040;
}
.btn_car__add_product{
  display: block;
  height: 40px;
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: #404040;
  padding: 11px 26px;
  background: #BED1E1;
  border-radius: 3px;
  cursor: pointer;
  transition: all .3s;

  &:hover{
    background-color: #E1CEBE;
    color: #404040;
  }
}