.section__about_company{
  margin-bottom: 35px;
}
.about_company__title{
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #404040;
  margin-bottom: 30px;
}
.about_company_text{
  padding-bottom: 22px;
  &>p{
    margin-bottom: 22px;
    font-family: 'Fira Sans';
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    letter-spacing: 0.02em;
    color: #404040;

    &:last-child{
      margin-bottom: 0;
    }
  }
}
