.article__slider{
  width: 100%;
  height: 450px;
  margin-bottom: 30px;
}
#carouselExampleIndicators{
  height: 100%;
}
.carousel-inner{
  display: flex;
  align-items: center;
  
  height: 100%;
  background-image: url('/src/assets/img/bg_slider.svg');
  background-position: cover;
}
.baner__title{
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 665px;
  height: 114px;
  margin: 0 auto;
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  color: #FFFFFF;
  background: rgba(#404040, 0.3)
}
.carousel-indicators>li{
  opacity: 1;
  display: block;
  width: 34px;
  height: 10px;
  background: #566E98;
  border-radius: 6px;
  border: none;
  margin-right: 20px;

  &:last-child{
    margin-right: 0;
  }
  &.active{
    background: #fff;
  }
}
.carousel-control-prev, .carousel-control-next{
  width: 70px;
  height: 100%;
  opacity: 1;
  transition: all .3s;
  &:hover{
    background: rgba(#E1CEBE, .6);

    &>.carousel-control-next-icon, .carousel-control-prev-icon{
      background-color: #fff;
    }
  }
}
.carousel-control-next-icon, .carousel-control-prev-icon{
  height: 36px;
  width: 22px;
  background-color: #566E98;;
  background-image: none;
  mask-image: url('/src/assets/img/Vector.svg');
  transition: all .3s;
}

.carousel-control-prev-icon{
  transform: scale(-1, 1);
}