$color_gray:#EBEDEE;

.header__head .wrapper{
 display: flex;
 justify-content: space-between;
 align-items: center;
 flex-direction: row;
 flex-wrap: wrap;
 min-height: 40px;
}
.header__head{
  width: 100%;
  background-color: #EBEDEE;
}
.nav__header__list{
  display: flex;
}
.nav__header__item{
  margin-right: 55px;
}
.nav_header__btn{
  height: 22px;
  
  font-family: 'Fira Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #404040;
  transition: all .3s;

  &:hover{
    color:#7c7c7c;
  }
}
.header__address__phone{
  display: flex;
  margin-bottom: 0;

  &>p{
    font-family: 'Fira Sans';
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 0em;
    margin: 10px 16px;
  }
}


.heder__main{
  width: 100%;
}
.heder__main .wrapper{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 125px;
  height: 125px;
}
.header__logo_block{
  height: 101px;
  width: 127px;
  margin: 5px;
}
.header__logo_img{
  display: block;
  height: 101px;
  width: 127px;
  background-image: url('/src/assets/img/logo.svg');
  background-size: cover;
}
.search_block{
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 5;
  max-width: 520px;
  height: 100%;
}
.tagline__title{
  font-family: 'Fira Sans';
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.02em;
  margin-bottom: 18px;
}
.search__input{
  height: 40px;
  border: 1px solid #E1CEBE;
  border-radius: 3px;
  padding: 0 20px;
  color: #806956;
  font-family: 'Fira Sans';
  font-weight: 400;
  line-height: 14px;

  &::placeholder{ 
    color: #E1CEBE; 
    opacity: 1;
  }
}
.header__cart{
  display: flex;
  align-items: center;
  height: 78px;
  width: 134px;
  margin: 16px;
  border-radius: 3px;
  background: #EBEDEE;
}
.cart__icon{
  background-image: url('/src/assets/img/cart.svg');
  height: 39px;
  width: 41px;
  margin-left: 13px;
}
.cart__count{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 43px;
  height: 25px;
  margin: 0 15px;
  background: #BED1E1;
  border-radius: 3px;

  font-family: 'Fira Sans';
  font-size: 14px;
  font-weight: 500;
  line-height: 19px;
  letter-spacing: 0em;

}

.nav__bar{
  width: 100%;
  min-height: 60px;
  margin-bottom: 30px;
  background-color: #E1CEBE;

  &>.wrapper{
    display: flex;
  }
}
.bar__list{
  display: flex;
  flex-wrap: wrap;

}
.bar__item{
  
  margin: 10px 0;
  border-radius: 3px;
  height: min-content;
  transition: all .3s;

  &:hover{
    background: #FFFFFF;
  }
}
.bar__linck{
  display: block;
  width: 100%;
  height: 100%;
  padding: 9px 50px;
  font-family: 'Fira Sans';
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  color: #404040;
  white-space: nowrap;
  transition: all .3s;

  &:hover{
    color: #404040;
  }
}

