
.section__news{
  background: #EBEDEE;
  border-radius: 3px;
  padding: 22px 28px;
}
.news__title{
  font-family: 'Fira Sans';
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 26px;
}

.news__item{
  margin-bottom: 16px;
}
.news__item__date{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 64px;
  height: 22px;
  margin-bottom: 12px;
  background: #BED1E1;
  border-radius: 3px;
  font-family: 'Fira Sans';
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #404040;
}
.news__item__text{
  font-family: "Fira Sans";
  font-weight: 500;
  font-size: 14px;
  line-height: 19px;
  color: #404040;
}